@import url(https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@300&family=Source+Sans+Pro&display=swap);
body {
  font-family: 'Source Sans Pro', sans-serif;
  background-color: #EFEFEF;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Montserrat Alternates', sans-serif;
}

img {
    width: 100%
}

main {
  min-height: 85vh;
}

.navigation-bar {
  font-size: 1.2em;
  padding: 18px;
}

.navigation-bar div {
  text-align: right;
}

.navbar-brand {
  margin-right: 0;
  font-size: 1.4em;
}

.dropdown-menu.show {
  background-color: #343a40;
}

.spinner-div {
  display: flex;
  justify-content: center;
}

.btn {
  border-radius: 35px;
  background-color: #ffffff00;
  color: #008650;
  transition: .5 ease;
  border: 1px solid #008650;
}

.btn.btn-dark:active
{
    background-color:#00ac67;
    border: 1px solid #00ac67
}

.btn:hover {
  background-color: #008650;
  color: white;
  border: 1px solid #008650;
}

.btn-primary:focus {
  color: #fff;
  background-color: #008650;
  border-color: #0062cc;
  box-shadow: 0 0 0 0.2rem rgb(38 143 255 / 50%);
}

.btn-primary:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #01965a;
  border-color: #005cbf;
}

h5 {
    margin-bottom: 40px;
}

.home-page {
    max-width: 100%;
}

.hero {
    
    background-repeat: no-repeat;
    background-size: cover;
    color: #353535;
    height: 80vh;
    margin: 7px;
    transition: 0.5s ease;
    max-width: 100%;
}



.hero .container {
    height: 100%;
}

.hero .row {
    text-align: center;
}

.hero h1 {
    font-size: 4em;
}

.info-block {
    margin-right: 0;
    margin-left: 0;
}
.card-image {
    object-fit: cover;
    height: 100%;
}

.card-image-wrapper {
    height: 232px;    
}

.labels {
    padding: 0 15px;
}

.label input {
    width: 100%;
    font-size: 0.7em;
    font-weight: 600;
    color: #008650;
    border: 1px solid #008650;
    background-color: #01437c00;
    border-radius: 5px;
}

.labels .label {
    margin: 0 0 10px 0;
    padding: 0 0 0 10px;
}

.label input.active {
    background-color: #008650;
    color: white;
}

.label input:hover{
    background-color: rgba(0, 133, 78, 0.24);
}

.label input.active:hover {
    background-color: #005231;
}

.ing-filter {
    width: 100%;
}
.spinner {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border-width: 4px;
  border-style: solid;
  border-top-color: #008650;
  border-left-color:#a3a3a3;
  border-right-color: #a3a3a3;
  border-bottom-color: #a3a3a3;
  animation: spinner 1s infinite ease-in-out
}


@keyframes spinner {
  from {
      transform: rotate(0deg);
  }

  to {
      transform: rotate(360deg);
  }
}
.recipes-section {
    margin-right: 0;
}

.glass-img {
    width: 60%;
}

.recipe-card {
    margin: 20px 0;
}

.card {
    border: 1px solid #008650;
    border-radius: 1px;
}

.recipe-card .card-body {
    background-color: white;
}

.recipe-card h5 {
    font-size: 0.3em;
}

.card-img, .card-img-top {
    border-radius: 0;
}

.health-labels {
    margin: 5px;
    font-size: 0.7em;
    border: 1px solid black;
    border-radius: 5px;
    padding: 3px;
    width: 100%;
    text-align: center;
}

.recipe-img {
    height: 350px;
    object-fit: cover;
    margin-bottom: 15px;
}

.nutrients-data {
    margin: 20px;
    list-style: none;
    width: 100%;
}

.comment {
    margin: 20px;
}

.comment img {
    width: 40px;
    height: 40px;
    object-fit: cover;
    border-radius: 50%;
}

.comment h6 {
    display: inline;
    margin-left: 10px;
}

.servings {
    position: absolute;
    top: 360px;
    right: 20px;
}

.go-back {
    margin-top: 20px;
}

.time {
    position: absolute;
    top: 0px;
    right: 20px;
}
.add-ing-btn {
    align-self: flex-end;
}

.add-ing-btn button {
    width: 100%;
}

.add-step-btn {
    align-self: flex-end;
}

.add-step-btn button {
    width: 100%;
}

.item-col {
    padding: 0;
}

.item-list {
    padding-left: 0;
    padding-right: 0;
}

.delete-button {
    float: right;
    background-color: rgb(228, 50, 50);
    border: 1px solid rgb(228, 50, 50)
}

.delete-button:hover {
    background-color: rgb(187, 0, 0);
    border: 1px solid rgb(187, 0, 0)
}

.delete-button img {
    width: 15px;
}
.card-image {
    object-fit: cover;
    height: 100%;
}

.card-image-wrapper {
    height: 232px;    
}

.user-card {
    margin: 20px 0;
}
.user-recipes {
    margin-right: 0;
}

.new-recipe-btn {
    margin-bottom: 40px;
    margin-top: 5px;
}

.avatar-section {
    margin-bottom: 20px;
}

.avatar-section img {
    width: 150px;
    height: 150px;
    object-fit: cover;
    border-radius: 50%;
    display: block;
    background-color: red;
}

.modal-dialog {
    max-width: 800px;
  }

.avatar-row {
    margin-left: 30px;
}

.profile-btn {
    display: flex;
    justify-content: space-around;
}

.profile-details-btn {
    width: 45%;
}

.delete-btn {
    background-color: rgb(228, 50, 50);
    transition: .5 ease;
    color: white;
    border: 0;
}

.delete-btn:hover {
    background-color: rgb(187, 0, 0);
}

.edit-recipe-btn {
    display: block
}

.profile-img {
    margin-bottom: 20px;
}

.delete-account-btn {
    margin-top: 100px;
}

.delete-user-btn {
    margin-top: 100px;
}
.nav-img {
    width: 40px;
    height: 40px;
    object-fit: cover;
    border-radius: 50%;
}

.logo-nav {
    width:150px;
}

.navigation-bar {
    background-color: #EFEFEF;
    border-bottom: 1px solid #008650;
}

.navbar-light .navbar-nav .nav-link {
    color: #008650;
}

.user-nav {
    display: flex;
    justify-content: flex-end;
    margin-left: 15px;
}
footer {
    background-color: #008650;
    color: white;
    text-align: center;
    padding: 3px;
    position: -webkit-sticky;
    position: sticky;
    width: 100%;
    font-size: .7em;
    bottom: 0;
    margin-top: 30px;
}
